.App {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}
h1{
  font-family: 'Fontdiner Swanky', cursive;
  color: #fff;
  letter-spacing: 2px;
  animation: slideup 1s;
}
h1::first-letter{
  font-size: 1.4em;
  color: #b9bae2;
}
@keyframes slideup {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}


