.search-box{
    -webkit-appearance: none;
    appearance: none;
    border: solid 1px #ddd;
    padding: 10px 15px;
    margin: 0 20px 30px;
    font-size: 1.1rem;
    border-radius: 10px;
    transition: .3s;
}
.search-box:focus{
    outline: none;
    padding: 12px 17px;
    margin-bottom: 26px;
}